<template>
  <div>
    <v-row>
      <v-col
        cols="12"
        sm="6"
        md="4"
        v-for="(assignment, index) in allAssignments"
        :key="index"
        @click="viewAssignment(assignment)"
        :disabled="assignment.answers == null"
      >
        <v-card raised outlined>
          <v-card-title>
            <v-row>
              <v-col cols="1">
                <v-icon>mdi-school</v-icon>
              </v-col>
              <v-col cols="10">
                {{ assignment.Student.firstName }}
                <span v-if="assignment.Student.middleName">
                  {{ assignment.Student.middleName }}
                </span>
                {{ assignment.Student.lastName }}
              </v-col>
            </v-row>
          </v-card-title>
          <v-card-text>
            Ansered: {{ assignment.isAnswered }}
            <br />
            Mark: {{ assignment.mark }}
            <br />
            Reviewed: {{ assignment.isReviewed }}
          </v-card-text>
        </v-card>
      </v-col>

      <v-snackbar v-model="notAnsweredDialog" color="error">
        <h2>This student didn't answer that homework!</h2>
      </v-snackbar>
    </v-row>
  </div>
</template>

<script>
import HomeworkService from "../services/HomeworkService";
export default {
  data() {
    return {
      homework: {},
      allAssignments: [],
      homeworkId: Number,
      notAnsweredDialog: false
    };
  },
  created() {
    this.homeworkId = this.$route.params.homeworkId;
    HomeworkService.GetHomework(this.homeworkId).then(homework => {
      this.homework = homework;
    });

    HomeworkService.GetAssignments(this.homeworkId).then(allAssignments => {
      this.allAssignments = allAssignments;
    });
  },
  methods: {
    viewAssignment(assignment) {
      if (assignment.isAnswered) {
        this.$router.push({
          name: "ViewAssignment",
          params: {
            assignmentId: assignment.id,
            homeworkId: this.homeworkId,
            studentId: assignment.studentId
          }
        });
      } else {
        this.notAnsweredDialog = true;
      }
    }
  }
};
</script>

